
import './App.css';
import '@tensorflow/tfjs-backend-webgl';
import {load} from "@tensorflow-models/qna";
import { WelcomeScreen } from './Components/WelcomeScreen';
import {MainUI} from './Components/MainUI';

function App() {
  let model;
  async function loadModels() {
    model = await load();
  }
  async function findAnswer(question, passage) {
    let answer = await model.findAnswers(question, passage);
    // console.log(answer);
    return answer;
  }
  return (
    <div className="App">
      <MainUI findAnswer={findAnswer}/>
      <WelcomeScreen qnaload={loadModels}/>
    </div>
  );
}

export default App;
