import React from 'react'

function MainUI(props) {
    let [inputText, setInputText] = React.useState("");
    let [questionText, setQuestionText] = React.useState("");
    let [answerText, setAnswerText] = React.useState("Answer");
    let [isDialogOpened, setIsDialogOpened] = React.useState(false);
    async function findAnswer() {
        if(inputText !== "" && questionText !== ""){
            let answer = await props.findAnswer(questionText, inputText);
            if(answer.length > 0) setAnswerText(answer[0].text);
            else setAnswerText("No answer found.");
            setIsDialogOpened(true);
        }
    }
    function handleInputChange(inputElement) {
        setInputText(inputElement.value);
    }
    function handleQuestionChange(inputElement) {
        setQuestionText(inputElement.value);
    }
    const closeDialog = () => setIsDialogOpened(false);
  return (
    <div className='mainUI'>
        <div className='mainUI-title'>Quick</div>
        <textarea className='mainUI-textarea' value={inputText} placeholder='Enter input text...' onInput={e=> handleInputChange(e.currentTarget)}></textarea>
        <textarea className='mainUI-textarea question' value={questionText} placeholder='Enter question text...' onInput={e=> handleQuestionChange(e.currentTarget)}></textarea>
        <div className='mainUI-bottomButtons'>
            {/* <button className='mainUI-secondaryButton' disabled={true}>Translate</button> */}
            <button className='mainUI-primaryButton' onClick={findAnswer}>Answer</button>
        </div>
        <div className={isDialogOpened ? 'dialog' : "dialog hide"}>
            <div className='dialogName'>Answer</div>
            <div className='dialogAnswer'>{answerText}</div>
            <button className='closeDialog' onClick={closeDialog}>Close</button>
        </div>
    </div>
  )
}
export {MainUI};