import React from 'react'

function WelcomeScreen(props) {
    let [isStarting, setIsStarting] = React.useState(false);
    let [started, setStarted] = React.useState(false);
    async function startup() {
        setIsStarting(true);
        await props.qnaload();
        setStarted(true);
    }
  return (
    <div className={started ? "welcomeScreen welcomeScreen-started" : "welcomeScreen"}>
        <div className='welcomeScreen-header'>
            <div className='header-appname'>
                Welcome to <span>Quick</span>
            </div>
            <button className={isStarting ? 'header-start header-start_starting' : "header-start"} onClick={startup}>
                <svg width="20" height="19" viewBox="0 0 20 19" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M9.99121 18.7422C14.9746 18.7422 19.0879 14.6289 19.0879 9.6543C19.0879 4.67969 14.9658 0.566406 9.98242 0.566406C5.00781 0.566406 0.90332 4.67969 0.90332 9.6543C0.90332 14.6289 5.0166 18.7422 9.99121 18.7422ZM13.542 10.1729L8.27734 13.2578C7.84668 13.5127 7.3457 13.293 7.3457 12.8535V6.47266C7.3457 6.02441 7.87305 5.82227 8.27734 6.05957L13.542 9.16211C13.9199 9.39062 13.9287 9.94434 13.542 10.1729Z" fill="#fff"/>
                </svg>
                Start Quick
                <div className={isStarting ? "loadingFilter" : "hide loadingFilter"}>Starting...</div>
            </button>
        </div>
        <div className='welcomeScreen-credits'>
            developed by <span>Vladimirov Igor</span>
        </div>
    </div>
  )
}

export {WelcomeScreen};